import { Component, OnInit } from '@angular/core';
import { TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-sopa',
  templateUrl: './sopa.component.html',
  styleUrls: ['./sopa.component.css']
})
export class SopaComponent{
  nombregrupo = 'Madugandi';
  nombresimulador = 'Adventurer Mission | ';
  daynumber = '9';
  valor = 'CCDEEHHLOPPRRRV';
  value: any;
  mayuscula: string;
  acertado: boolean = false ;
  upperCase(input: string) {
    if(input.toUpperCase() == this.valor){
      this.acertado = true;
    }
    else{
      this.acertado = false;
    }
  };
  modalRef: BsModalRef;
  constructor(private modalService: BsModalService) {}

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template);
  }
}
