import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Router } from '@angular/router';
import { Component, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-day13',
  templateUrl: './day13.component.html',
  styleUrls: ['./day13.component.css'],
})
export class Day13Component {
  nombregrupo = 'Madugandi';
  nombresimulador = 'Adventurer Mission | ';
  daynumber = '7';
  //valor:number;
  valorM = 42;
  valorH = 24;
  input1: any;
  input2: any;
  acertado: boolean = false;
  edad() {
    if(this.input1 == this.valorM && this.input2 == this.valorH){
      this.acertado = true;
    }
    else{
      this.acertado = false;
    }
  };
  modalRef: BsModalRef;
  constructor(private modalService: BsModalService) {}

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template);
  }
}